import { useEffect, useState } from "react";
import { Field, Form } from "react-final-form";
import { Link } from "react-router-dom";
import PhoneInput from "react-phone-number-input";
import { InputField, AlertDismissible, Error } from "common/components";
import { UserService } from "client/services";
import loginBanner from "client/assets/images/login-banner.png";
import RegistrationSteps from "./registationSteps";

import "react-phone-number-input/style.css";

const Register = (props) => {
  const required = `Required`;
  const [message, setMessage] = useState();
  const [errorData, setErrorData] = useState(null);
  const [formValues, setFormValues] = useState(null);
  const [isLoading, setIsLoading] = useState(false);

  useEffect(() => {
    document.body.classList.add("account-page");
    return () => {
      document.body.classList.remove("account-page");
    };
  }, []);

  const validate = async (values) => {
    const errors = {};
    if (!values.firstName) {
      errors.firstName = required;
    }
    if (!values.lastName) {
      errors.lastName = required;
    }
    if (!values.email) {
      errors.email = required;
    } else if (
      !values.email
        .toLowerCase()
        .match(
          /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
        )
    ) {
      errors.email = "Invalid Email format!";
    }

    if (!values.mobile) {
      errors.mobile = required;
    }
    if (!values.password) {
      errors.password = required;
    } else if (values.password.length < 6 || values.password.length >= 40) {
      errors.password = "Password length must be between 6 and 40";
    }
    return errors;
  };

  const onSubmit = async (values) => {
    setErrorData(null);
    setIsLoading(true);
    // // await sleep(1500);
    console.log(`onSubmit :: ${JSON.stringify(values, 0, 2)}`);
    const response = await UserService.isUserRegistered(values.email); //await AuthService.register(values.email, values.password);
    setIsLoading(false);

    if (!!!response.success && !response.success) {
      setMessage(!!response.message && response.message);
      return;
    }

    //open the wizard
    setFormValues(values);

    //navigate("/login", { ...values });
  };

  const PhoneAdapter = ({ input, placeholder }) => (
    <PhoneInput
      className="form-control floating"
      {...input}
      placeholder={placeholder}
      Style="border:0px;"
    />
  );
  const errorHandle = (errData) => {
    setFormValues(null);
    setMessage(errData);
  };
  const myForm = (
    <Form
      validate={validate}
      onSubmit={onSubmit}
      // initialValues={{ doctorData }}
      render={({
        handleSubmit,
        submitError,
        form,
        submitting,
        pristine,
        values,
      }) => (
        <div className="col-md-12 col-lg-6 login-right">
          <div className="login-header">
            <h3>Register to Your Medical Portal</h3>
          </div>
          <form onSubmit={handleSubmit} className="needs-validation" noValidate>
            <div className="invalid-feedback d-block mb-2">{message}</div>
            <div className="row form-row" style={{ marginBottom: "-20px" }}>
              <div className="col-12 col-sm-6">
                <div
                  className={`form-group form-focus mt-4 ${
                    values?.firstName?.length > 0 ? "focused" : ""
                  }`}
                >
                  <Field
                    name="firstName"
                    className="form-control floating"
                    label="First Name"
                    type="text"
                    label_type="focus_label"
                    component={InputField}
                  />
                </div>
              </div>
              <div className="col-12 col-sm-6">
                <div
                  className={`form-group form-focus mt-4 ${
                    values?.lastName?.length > 0 ? "focused" : ""
                  }`}
                >
                  <Field
                    name="lastName"
                    className="form-control floating"
                    label="Last Name"
                    type="text"
                    label_type="focus_label"
                    component={InputField}
                  />
                </div>
              </div>
            </div>
            <div
              className={`form-group form-focus mt-4 ${
                values?.email?.length > 0 ? "focused" : ""
              }`}
            >
              <Field
                name="email"
                className="form-control floating"
                label="Email"
                type="text"
                label_type="focus_label"
                component={InputField}
              />
            </div>
            <div
              className={`form-group form-focus mt-4 ${
                values?.mobile?.length > 0 ? "focused" : ""
              }`}
            >
              {/* <Field
                name="mobile"
                className="form-control"
                label="Mobile Number"
                type="text"
                label_type="focus_label"
                component={InputField}
              /> */}
              <Field
                name="mobile"
                defaultCountry="TR"
                international
                className="form-control floating"
                placeholder="Enter phone number"
                label="Mobile Number"
                label_type="focus_label"
                component={PhoneAdapter}
              />
              <Error name={"mobile"} showerrorbeside="true" />
            </div>
            <div
              className={`form-group form-focus mt-4 ${
                values?.password?.length > 0 ? "focused" : ""
              }`}
            >
              <Field
                name="password"
                className="form-control floating"
                label="Create Password"
                type="password"
                label_type="focus_label"
                component={InputField}
              />
            </div>
            <button
              className="btn btn-primary btn-block btn-lg login-btn  mt-5"
              type="submit"
              disabled={isLoading}
            >
              {!isLoading ? `Signup` : ""}
              {isLoading && (
                <span className="spinner-border spinner-border-sm mb-1"></span>
              )}
            </button>
            {/* Social Medica Login */}
            {/* <div className="login-or">
    <span className="or-line"></span>
    <span className="span-or">or</span>
  </div>
  <div className="row form-row social-login">
    <div className="col-6">
      <a href="#0" className="btn btn-facebook btn-block">
        <i className="fab fa-facebook-f mr-1"></i> Login
      </a>
    </div>
    <div className="col-6">
      <a href="#0" className="btn btn-google btn-block">
        <i className="fab fa-google mr-1"></i> Login
      </a>
    </div>
  </div> */}
            {/* /Social Medica Login */}
            <div className="text-center dont-have">
              {`Already have an account? `}
              <Link to="/login">Login</Link>
            </div>
          </form>
        </div>
      )}
    />
  );
  return (
    <div className="content">
      <div className="container-fluid">
        <div className="row">
          <div className="col-md-8 offset-md-2">
            <div className="account-content">
              {errorData && (
                <AlertDismissible
                  type={errorData.messgeType}
                  boldMsg={errorData.messageTitle}
                  msg={errorData.messageBody}
                />
              )}
              <div className="row align-items-center justify-content-center">
                <div className="col-md-7 col-lg-6 login-left">
                  <img
                    src={loginBanner}
                    className="img-fluid"
                    alt="Register to Your Medical Portal"
                  />
                </div>
                {formValues === null ? (
                  <>{myForm}</>
                ) : (
                  <RegistrationSteps
                    values={formValues}
                    OnError={errorHandle}
                  />
                )}
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Register;
